import { BrowserModule } from '@angular/platform-browser';
import { platformBrowser } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { ReactiveFormsModule } from '@angular/forms';
import { Http, HttpModule, RequestOptions } from '@angular/http';
import { AuthHttp, AuthConfig } from 'angular2-jwt';
import { EquipesComponent } from './equipes/equipes.component';
import { HttpClientModule } from '@angular/common/http';

import { AppComponent } from './app.component';
//import { Routing } from './app.routing';
import { RouterModule } from '@angular/router';
import { routes} from './app.routes';
import { AuthGuard } from './_guard/index';

import { AuthenticationComponent } from './authentication/authentication.component';
import { AuthenticationService } from './authentication/authentication.service';
import { HomepageComponent } from './homepage/homepage.component';
import { PostComponent } from './post/post.component';
import { PostRepository } from './post/post-repository.service';
import { RencontresComponent } from './rencontres/rencontres.component';
import { NavbarComponent } from './navbar/navbar.component';
import { GroupesComponent } from './groupes/groupes.component';
import { ClassementComponent } from './classement/classement.component';
import { ListeEquipeComponent } from './liste-equipe/liste-equipe.component';

export function authHttpServiceFactory(http: Http, options: RequestOptions) {
    return new AuthHttp( new AuthConfig({}), http, options);
}




@NgModule({
     declarations: [
         AppComponent,
         AuthenticationComponent,
         HomepageComponent,
         PostComponent,
         EquipesComponent,
         RencontresComponent,
         NavbarComponent,
         GroupesComponent,
         ClassementComponent,
         ListeEquipeComponent
     ],
     imports: [
         BrowserModule,
         HttpClientModule,
         ReactiveFormsModule,
         HttpModule,
         //Routing,
         RouterModule.forRoot(routes)
     ],
     providers: [
     {
         provide: AuthHttp,
         useFactory: authHttpServiceFactory,
         deps: [ Http, RequestOptions ]
     },
         AuthGuard,
         AppComponent,
         AuthenticationService,
         PostRepository
     ],
     bootstrap: [AppComponent]
})
export class AppModule { }